/* EasyData grid */

.keg-container * {
    outline: none;    
}

.keg-container {
    overflow: hidden;
}

.keg-header {
    background-color: #f8f8f8;
    border-bottom: 1px solid #babfc7;
    overflow: hidden;
    flex: 0 0 auto;
}

.keg-root {
    font-family: "Roboto", Arial, serif;
    display: flex;
    flex-direction: column;
    border: 1px solid #babfc7;
}

.keg-body {
    overflow: hidden;
    flex: 1;
}

.keg-body-viewport {
    overflow-x: auto;
    overflow-y: auto;
    height: 100%;
    border: 1px solid transparent;
}

.keg-body-viewport:focus, .keg-body-viewport:focus-within {
    border: 1px solid #838383;
}

.keg-header-row {
    min-height: 30px;
    display: flex;
    font-size: 12px;
    font-weight: 600;
    color: rgba(0,0,0,.54);
}

.keg-row {
    display: flex;
    font-size: 12px;
    background-color: #fff;
    color: #181d1f;
    outline: none;
    border-color: #dde2eb;
    border-width: 1px;
    border-bottom-style: solid;
    height: 26px;
    align-items: center;
}

.keg-row:not(.keg-row-active):not(.keg-row-totals):hover {
    background-color: #fffae9;
}

.keg-row-odd {
    background-color: #fcfcfc;
}

.keg-row-active {
    background-color: #fff3cf;
}

.keg-cell, .keg-header-cell {
    display: flex;
}

.keg-header-cell {
    position: relative;
    align-items: center;
    padding: 2px 6px;
}

.keg-header-cell-resize {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 2;
    height: 100%;
    width: 8px;
    top: 0;
    right: -4px;
}

.keg-header-cell-resize:after {
    content: "";
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50% - 1px);
    width: 2px;
    height: 50%;
    top: 25%;
    background-color: rgba(186,191,199,.5);    
}

.keg-header-cell-label {
    width: 100%;
    text-align: center;
    word-break: keep-all;
    -ms-word-wrap: keep-all;
}

.keg-cell {
    padding: 0 4px 0 10px;
    border-right-width: 1px;
    border-right: solid transparent;
    align-items: center;
    height: 100%;
    outline: none;
}

.keg-cell-value {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-height: 100%;
}

/*
.keg-row .keg-cell:first-child, .keg-header-row .keg-header-cell:first-child {
    flex: 0 0 55px;
    min-width: 55px;
}
*/

.keg-cell-align-left, .keg-cell-value-align-left {
    text-align: left;
}

.keg-cell-align-right, .keg-cell-value-align-right {
    text-align: right;
}

.keg-cell-align-center, .keg-cell-value-align-center {
    text-align: center;
}

.keg-cell-value-bool {
    margin: 0 auto;
    width: auto;
}

.keg-cell-value-true::before {
    content: "\2714";
    color: #28a745;
    font-size: 16px;
}

.keg-cell-value-false::before {
    content: "\2716";
    color: #dc3545;
    font-size: 16px;
}

.keg-header-btn-plus {
    position: relative;
    height: 23px;
    width: 23px;
}

.keg-header-btn-plus a {
    background: url('data:image/png;base64,') no-repeat;
    width: 100%;
    height: 100%;
    display: block;
}

.keg-header-btn-plus a:hover {
    background-position: -25px 0 !important;
}

/* Pagination */
.keg-pagination-wrapper {
    display: inline-flex;
    margin-left: 10px;
}

.keg-pagination {
    display: inline-flex;
    padding-left: 0;
    margin: 0;
    border-radius: 4px;
}

.keg-page-item {
    display: inline;
}

.keg-page-link {
    font-size: 12px;
    background-color: #fff;
    border: 1px solid #ddd;
    color: #428bca;
    float: left;
    line-height: 1.42857;
    margin-left: -1px;
    padding: 5px 10px;
    position: relative;
    text-decoration: none;
}

.keg-page-link:hover, .keg-page-link:focus {
    background-color: #eee;
    border-color: #ddd;
    color: #2a6496;
    border-bottom: #ffd148 2px solid !important;
}

.keg-page-item.active > .keg-page-link, .keg-page-item.active > .keg-page-link:hover, .keg-page-item.active > .keg-page-link:focus {
    background-color: #428bca;
    border-color: #428bca;
    color: #fff;
    cursor: default;
    z-index: 2;
}

.keg-page-item.disabled > .keg-page-link, .keg-page-item.disabled > .keg-page-link:hover, .keg-page-item.disabled > .keg-page-link:focus {
    background-color: #fff;
    border-color: #ddd;
    color: #999;
    cursor: not-allowed;
}

.keg-page-item:first-child > .keg-page-link {
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
    margin-left: 0;
}

.keg-page-item:last-child > .keg-page-link {
    border-bottom-right-radius: 3px;
    border-top-right-radius: 3px;
}

.keg-page-sizes {
    display: inline-flex;
    margin-left: 20px;
}

.keg-page-sizes-select {
    font-size: 12px;    
    height: auto !important;
}

.keg-page-sizes-select::after {
    height: .6em !important;
    width: .6em !important;
    margin-top: -.6em !important;
    border-width: 2px !important;
}

.keg-page-sizes-label {
    margin-left: 10px;
    font-size: 16px;
    color: rgba(0,0,0,.54);
    padding: 5px 0;
    white-space: nowrap;
}

.keg-footer {
    display: flex;
    /*justify-content: space-between;*/
    margin-top: 5px;
    flex-wrap: wrap;
}

.keg-page-info {
    padding: 5px 10px;
    font-size: 16px;
    white-space: nowrap;
    flex: 1 1 auto;
    text-align: end;
    color: rgba(0,0,0,.54);
}

.keg-page-info > span {
    color: #212529;
}

.keg-row-totals {
    font-weight: bold;
}

.keg-totals-lv0 {
    background-color: #ACE1AF;
}

.keg-totals-lv1 {
    background-color: #D0F0C0;
}

.keg-totals-lv2 {
    background-color: #D0F2C0;
}

.keg-totals-lv3 {
    background-color: #D0F4C0;
}

.keg-totals-lv4 {
    background-color: #D0F6C0;
}

.keg-totals-lv5 {
    background-color: #D0F8C0;
}

.keg-sortable-helper {
    background-color: white;
    padding: 3px 20px;
    border: 2px solid #fb9c2f;
    border-radius: 4px;
}


.eqjs-chart-main, .eqjs-chart-content, .keg-container, .keg-root {
    height: 100%;
}

.eqjs-chart-content canvas {
    max-height: 100%;
}

.question-mark {
    position: relative;
    background: url('data:image/png;base64,') no-repeat center;
    width: 20px;
    height: 20px;
    margin-left: 5px;
}
/* EasyData dialogs */

.kdlg-modal, .kdlg-modal-background {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
}

.kdlg-modal-background {
    background-color: rgba(10,10,10,0.5);
}

.kdlg-modal {
    font-family: "Roboto", Arial, serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;

    align-items: center;
    display: none;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    position: fixed;
    z-index: 9999;
}

.kdlg-modal.is-active {
    display: flex;
}


.kdlg-modal-window, .kdlg-modal-window.size-default {
    margin: 0 20px;
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - 40px);
    overflow: hidden;
    -ms-overflow-y: visible;    
}

@media screen and (min-width: 481px), print {
    .kdlg-modal-window.size-small {
        margin: 0 auto;
        width: 400px;
    }
}

@media screen and (min-width: 769px), print {
    .kdlg-modal-window, .kdlg-modal-window.size-default {
        margin: 0 auto;
        width: 640px;
    }

    .kdlg-modal-window.size-large, .kdlg-modal-window.size-xl {
        margin: 0 20px;
        width: 100%;
    }
}

@media screen and (min-width: 1025px), print {
    .kdlg-modal-window.size-large {
        margin: 0 auto;
        width: 900px;
    }
}

@media screen and (min-width: 1383px), print {
    .kdlg-modal-window.size-xl {
        margin: 0 auto;
        width: 1200px;
    }
}


.kdlg-footer, .kdlg-header {
    align-items: center;
    background-color: #fff;
    display: flex;
    flex-shrink: 0;
    justify-content: flex-start;
    padding: 20px;
    position: relative;
    width: 100%;
}

.kdlg-footer.align-right {
    justify-content: flex-end;
}

.kdlg-footer.align-center {
    justify-content: center;
}

.kdlg-header {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.kdlg-header.has-border {
    border-bottom: 1px solid #dbdbdb;
}

.kdlg-footer {
	min-height: 10px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    height: auto;
    color: initial;
    margin: unset;
}

.kdlg-footer.has-border {
    border-top: 1px solid #dbdbdb;
}

.kdlg-header-title {
    color: #363636;
    flex-grow: 1;
    flex-shrink: 0;
    font-size: 1.6em;
    line-height: 1;
    margin: 0;
    padding: 0;
}

.kdlg-modal-close {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background-color: rgba(10,10,10,.2);
    border: none;
    border-radius: 290486px;
    cursor: pointer;
    pointer-events: auto;
    display: inline-block;
    flex-grow: 0;
    flex-shrink: 0;
    font-size: 0;
    height: 24px;
    max-height: 24px;
    max-width: 24px;
    min-height: 24px;
    min-width: 24px;
    outline: 0;
    position: relative;
    vertical-align: top;
    width: 24px;
}

.kdlg-modal-close::before, .kdlg-modal-close::after {
    background-color: #fff;
    content: "";
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
    transform-origin: center center;
}

.kdlg-modal-close::before {
    height: 2px;
    width: 50%;
}

.kdlg-modal-close::after {
    height: 50%;
    width: 2px;
}

.kdlg-modal-close:focus, .kdlg-modal-close:hover {
    background-color: rgba(255,1,1,.48);
}


.kdlg-body {
    -webkit-overflow-scrolling: touch;
    background-color: #fff;
    flex-grow: 1;
    flex-shrink: 1;
    overflow: auto;
    padding: 20px;
    font-size: 1em;
    color: #4a4a4a;
    width: 100%;
}


/** alerts start **/

.kdlg-alert-container {
    max-height: 150px;
    overflow-y: auto;
}

.kdlg-alert {
    padding: 20px;
    background-color: #e2f5ff;
    color: #004aef;
    min-width: 1px;
    min-height: 60px;
}

.kdlg-alert.success {
    color: #0c7905;
    background-color: #efffe6;
}

.kdlg-alert.warning {
    color: #e49e02;
    background-color: #fff7e5;
}

.kdlg-alert.error {
    color: red;
    background-color: #ffe2e2;
}

.kdlg-alert-closebtn {
    margin-left: 15px;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

/** alerts end **/


/** progress start **/
.kdlg-progress-line {
    width: 100%;
    overflow: hidden;
    background-color: #e0e0e0;
    padding: 3px;
    border-radius: 3px;
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, .2);
}

.kdlg-progress-line .fill {
    display: block;
    height: 22px;
    background-color: #659cef;
    border-radius: 3px;
    transition: width 500ms ease-in-out;
}

.kdlg-progress-line .fill.indeterminate  {
    position: relative;
    left: -200px;
    width: 200px;
    animation: kdlg-progress-line-loading 2s linear infinite;
    transition: none;
}

  @keyframes kdlg-progress-line-loading {
      from { left: -200px; }
      to {  left: 120%;}
  }

/** progress end **/
/* EasyData forms: buttons */

.kfrm-buttons {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.kfrm-buttons.align-right {
    justify-content: flex-end;
}

.kfrm-buttons.align-center {
    justify-content: center;
}

.kfrm-buttons:last-child {
    margin-bottom: -.5rem;
}

.kfrm-buttons .kfrm-button {
    margin-bottom: .5rem;
}

.kfrm-button {
    position: relative;
    vertical-align: top;
    line-height: 1.5;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 4px;
    box-shadow: none;
    display: inline-flex;
    font-size: 1em;
    height: 2.5em;
    -moz-appearance: none;
    -webkit-appearance: none;


    background-color: #fff;
    border-color: #dbdbdb;
    border-width: 1px;
    color: #363636;
    cursor: pointer;
    justify-content: center;
    padding-bottom: calc(.5em - 1px);
    padding-left: 1em;
    padding-right: 1em;
    padding-top: calc(.5em - 1px);
    text-align: center;
    white-space: nowrap;

    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -moz-user-select:none;
    user-select:none
}

.kfrm-button:not(:last-child) {
    margin-right: .5em;
}

.kfrm-button.is-hovered, .kfrm-button:hover {
    border-color: #b5b5b5;
    color: #363636;
}

.kfrm-button.is-focused, .kfrm-button:focus {
    outline: none;
    border-color: #3273dc;
    color: #363636;
}

.kfrm-button.is-focused:not(:active), .kfrm-button:focus:not(:active) {
    box-shadow: 0 0 0 0.125em rgba(50,115,220,.25);
}

.kfrm-button.is-active, .kfrm-button:active {
    border-color: #4a4a4a;
    color: #363636;
}

.kfrm-button[disabled], fieldset[disabled] .kfrm-button {
    background-color: #fff;
    border-color: #dbdbdb;
    box-shadow: none;
    opacity: .5;
    cursor: not-allowed;
}

.kfrm-button.is-loading {
    color: transparent!important;
    pointer-events: none;
}

.kfrm-button.is-loading::after {
    animation:spinAround .5s infinite linear;
    border:2px solid #dbdbdb;
    border-radius:290486px;
    border-right-color:transparent;
    border-top-color:transparent;
    content:"";
    display:block;
    height:1em;
    width:1em;

    left: calc(50% - (1em / 2));
    top: calc(50% - (1em / 2));
    position: absolute!important;
}

@keyframes spinAround {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}

/* SIZES */

.kfrm-button.size-small,
.kfrm-buttons.size-small .kfrm-button:not(.size-default):not(.size-medium):not(.size-large) {
    border-radius: 2px;
    font-size: .75rem;
}

.kfrm-button.size-default
.kfrm-buttons.size-default .kfrm-button:not(.size-small):not(.size-medium):not(.size-large) {
    font-size: 1rem;
}

.kfrm-button.size-medium,
.kfrm-buttons.size-medium .kfrm-button:not(.size-small):not(.size-default):not(.size-large) {
    font-size: 1.25rem;
}

.kfrm-button.size-large
.kfrm-buttons.size-large .kfrm-button:not(.size-small):not(.size-default):not(.size-medium) {
    font-size: 1.5rem;
}

/* SIZES END */



