    outline:0
}

.kdtp-tp-time-row input[type=range]:focus::-webkit-slider-thumb {
    background:#0099CC;
    border-color:#0099CC
}

.kdtp-tp-time-row input[type=range]:focus::-moz-range-thumb {
    background:#0099CC;
    border-color:#0099CC
}

.kdtp-tp-time-row input[type=range]:focus::-ms-thumb {
    background:#0099CC;
    border-color:#0099CC
}

.kdtp-tp-time-row input[type=range]::-webkit-slider-thumb {
    -webkit-appearance:none;
    box-sizing:border-box;
    height:12px;
    width:12px;
    border-radius:3px;
    border:1px solid #c5c5c5;
    background:#fff;
    cursor:pointer;
    -webkit-transition:background .2s;
    transition:background .2s;
    margin-top:-6px
}

.kdtp-tp-time-row input[type=range]::-moz-range-thumb {
    box-sizing:border-box;
    height:12px;
    width:12px;
    border-radius:3px;
    border:1px solid #c5c5c5;
    background:#fff;
    cursor:pointer;
    -moz-transition:background .2s;
    transition:background .2s
}

.kdtp-tp-time-row input[type=range]::-ms-thumb {
    box-sizing:border-box;
    height:12px;
    width:12px;
    border-radius:3px;
    border:1px solid #c5c5c5;
    background:#fff;
    cursor:pointer;
    -ms-transition:background .2s;
    transition:background .2s
}

.kdtp-tp-time-row input[type=range]::-webkit-slider-runnable-track {
    border:none;
    height:1px;
    cursor:pointer;
    color:transparent;
    background:0 0
}

