    height:1px;
    cursor:pointer;
    color:transparent;
    background:0 0
}

.kdtp-tp-time-row input[type=range]::-ms-fill-lower {
    background:0 0
}

.kdtp-tp-time-row input[type=range]::-ms-fill-upper {
