/*  Progress indicator  */
.eqjs-progress-win8 {
    width: 40px;
    height: 40px;
    margin: auto;
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
}

.eqjs-progress-win8 .wBall {
    position: absolute;
    width: 38px;
    height: 38px;
    opacity: 0;
    transform: rotate(225deg);
    animation: orbit 7.15s infinite;
}

.eqjs-progress-win8 .wBall .wInnerBall {
    position: absolute;
    width: 5px;
    height: 5px;
    background: #000000;
    left: 0;
    top: 0;
    border-radius: 5px;
}

.eqjs-progress-win8 #wBall_1 {
    animation-delay: 1.56s;
}

.eqjs-progress-win8 #wBall_2 {
    animation-delay: 0.31s;
}

.eqjs-progress-win8 #wBall_3 {
    animation-delay: 0.62s;
}

.eqjs-progress-win8 #wBall_4 {
    animation-delay: 0.94s;
}

.eqjs-progress-win8 #wBall_5 {
    animation-delay: 1.25s;
}

@keyframes orbit {
    0% {
        opacity: 1;
        z-index: 99;
        transform: rotate(180deg);
        animation-timing-function: ease-out;
    }

    7% {
        opacity: 1;
        transform: rotate(300deg);
        animation-timing-function: linear;
        transform-origin: 0;
    }

    30% {
        opacity: 1;
        transform: rotate(410deg);
        animation-timing-function: ease-in-out;
        transform-origin: 7%;
    }

    39% {
        opacity: 1;
        transform: rotate(645deg);
        animation-timing-function: linear;
        transform-origin: 30%;
    }

    70% {
        opacity: 1;
        transform: rotate(770deg);
        animation-timing-function: ease-out;
        transform-origin: 39%;
    }

    75% {
        opacity: 1;
        transform: rotate(900deg);
        animation-timing-function: ease-out;
        transform-origin: 70%;
    }

    76% {
        opacity: 0;
        transform: rotate(900deg);
    }

    100% {
        opacity: 0;
        transform: rotate(900deg);
    }
}
/*  End of Progress indicator  */



