/* Switch control */

.eqjs-ctrl-switch {
    position: relative;
    display: inline-block;
    margin-bottom: 0;
}

.eqjs-ctrl-switch > .switch-text {
    margin-left: 52px;
    margin-top: 2px;
    display: inline-block;
    cursor: pointer;
}

/* Hide default HTML checkbox */
.eqjs-ctrl-switch > input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.eqjs-ctrl-switch > .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 45px !important;
    height: 26px;
    background-color: #ccc;
    transition: .4s;
}

.eqjs-ctrl-switch > .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 21px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
}

.eqjs-ctrl-switch > input:checked + .slider {
    background-color: #2196F3;
}

.eqjs-ctrl-switch > input:focus + .slider {
    box-shadow: inset 0 0 0 1px #363636;
}

.eqjs-ctrl-switch > input:checked + .slider:before {
    transform: translateX(17px);
}

/* Rounded sliders */
.eqjs-ctrl-switch > .slider.round {
    border-radius: 26px;
}

.eqjs-ctrl-switch > .slider.round:before {
    border-radius: 50%;
}


